<template>
  <div class="main">
    <input type="submit" class="my_button" @click="random_recipe()" value="Random Recipe"/>
    <p class="recipe_name" ref="recipe_ref"></p>
    <!-- input ref="new_recipe_ref" type="text" class="new_recipe" placeholder="add new recipe" @keypress="new_recipe" v-model="input_text" /-->
    <select ref="dropDownMenu" v-model="selected" class="new_recipe">
      <option class="top_options">Vegetarische Gerichte</option>
      <option v-for="(value, name, index) in recipeList_veg" :key="index"> {{ name }} </option>
      <option class="top_options">Fleischgerichte</option>
      <option v-for="(value2, name2, index2) in recipeList_noveg" :key="index2"> {{ name2 }} </option>
    </select>
    <div class="all_recipes">
      <p class="all_heading">Alle der Kategorie {{ selected }} </p>
      <recipe :reci_var="data" v-for="(data, index) in category()" :key=index />
    </div>
  </div>
</template>

<script>
import recipe from "./components/recipe.vue"

export default {
  name: 'App',
  components: {
    recipe,
    
  },
  data () {
    return {
      recipeList_veg: {"Aufläufe": ["Spinat-Gemüse-Lasagne", "Vegetarisches Pastizio", "Gnocchi-Kürbis-Lauch-Auflauf", "Vegetarisches Moussaka",],
                       "Nudelgerichte": ["Vegetarische Tortelini-Pfanne", "Zucchini-Spaghetti", "Gnocchi-Pfanne mit Rosenkohl", "Gnocchi mit Pfifferlingen und Salbei"],
                       "Eintöpfe und Suppen": ["Weißkohl-Kartoffel-Eintopf", "Jamaikanische Kürbissuppe", "Kartoffelsuppe mit Champions", "Vegetarischer Borschtsch", "Elas Minestrone", "Gazpacho", "Türkische Gemüsesuppe", "Maghreb-Eintopf", "15 min Gemüse-Nudel-Suppe", "Petersiliensuppe", "Exotische Partysuppe", "Winterliche Maronensuppe", "Tomatensuppe", "Paprika-Möhrensuppe"],
                       "Indische Gerichte": ["Indisches Dal", "scharfes Indisches Gemüsecurry mit Blattspinat und Blumenkohl", "Indischer Blumenkohl", "Indisches Gemüsecurry mit Basmatireis"],
                       "Curries, Chilies und anderer Gemüsepamp": ["Asiatische Wok-Pfanne", "Spitzkohlcurry", "Kartoffelcurry mit Cashewnüssen", "Vegetarisches Chili", "Cashew-Gemüse-Indisch", "Thailändische Wokpfanne mit Kokosmilch", "Mualle- Auberginen und Linsen langsam geschmort"],
                       "Couscous-, Quinoa- und Reispfannen": ["Couscous-Gemüsepfanne mit leichter Käsesauce", "Couscous mit sommerlichem Gemüse", "Couscous-Gemüse-Curry", "Vegetarische Reispfanne mit Feta-Käse", "Quinoa mit Erbsen"],
                       "Bratlinge": ["Couscous-Bratlinge", "Polenta-Schnitten", "Vegetarische Frikadellen", "Couscous-Bratlinge mit Käse", "Möhrenfrikadellen"],
                       "Sonstige leckere Gerichte": ["Sommerliche Eierrolle", "Parmesan-Auberginen", "Vegetarische Kohlrouladen"],
                       "Kartoffelgerichte": ["Kartoffelgratin", "Rösti", "Fettarme Kartoffelspalten aus dem Ofen"],
                       "Gefülltes": ["Gefüllte vegane Paprika", "Mit Couscous gefüllte, Überbackene Zucchini", "gefüllte Paprika", "Gefüllte Spitzpaprika mit Couscous"],
                       "Süßes": ["Pfannkuchen", "Milchreis"]},
      recipeList_noveg: {"Suppen": ["Einfache Kartoffelsuppe", "Feine Linsensuppe", "Serbische Bohnensuppe", "Partysuppe à la Maike", "Sellerie-Schinkensuppe", "Graupensuppe von Ingwe", "Thai-Suppe", "3x Champion-Creme-Suppe", "Schneller Linseneintopf", "Maistopf Farmer Art", "Bunter Hackfleisch-Gemüse-Eintopf", "Krümeltigers Sauerkrauteintopf mit Rinderhack", "Hühnersuppe indisch"],
                         "Hähnchengerichte": ["Quinoa-Hähnchen-Pfanne", "Gemüse-Frikassee", "Hähnchen in würziger Kokossoße", "Hähnchencurry indisch in Kokosmilch", "Indisches Butter Chicken aus dem Ofen", "Chinesisches Hühnerfleisch mit Cashewnüssen", "Chicken Tikka Masala", "Hähnchentopf mit Kartoffeln", "Brasilianisches Hühnchen mit Kokosmilch und Mango", "Kürbis-Hähnchen-Curry", "Couscous-Pfanne", "Puten-Geschnetzeltes in Salbei-Knoblauch-Sauce"],
                         "Festliche Gerichte": ["Entenbrust Orange", "Hirschbraten in Sauerrahmsauce"],
                         "Aufläufe mit Fleisch": ["Lasagne", "Hack-Brokoli-Kartoffelauflauf", "Moussaka", "Süßkartoffel-Auflauf mit Hackfleisch"],
                         "Fischgerichte": ["Fisch mit Curry-Kräckerkruste", "Schwedischer Heringsauflauf", "Forelle in Folie", "Lachs-couscous-Päckchen", "Seelachsfilet auf Spinat", "Garnelen-Laksa", "Betreff: Garnelen"],
                         "Sonstiges": ["Tunesischer Couscous", "Koreanische Pfannkuchen mit 9 Köstlichkeiten", "Orientalische Auberginen-Hackfleisch-Schiffchen mit Joghurt und Reis", "Einfaches Kalbsgulasch", "Ungarischer Gulasch/Pörkölt", "Mexikanische Burritos", "Majas Quiche Lorraine", "Qiuche Lorraine", "Königsberger Klopse", "Fleischbällchen in Paprikasoße", "Flammkuchen", "Königsberger Klopse westpreußische Art", "Geschnetzeltes mit Paprika", "Kartoffel-paprika-Pfanne mit Hackfleisch", "Herzhafter Zucchini-Strudel", "Saltimbocca alla Romana", "Gefüllte Paprika nach Carstens Art",  "Schweinefleisch-süß-sauer", "Omas Kohlrouladen", "Rösti"]},
      input_text: "",
      selected: "",
      random_list: [],
      cat: undefined,
      meal: undefined,
      returnList: [],
      returnList2: [],
    }
  },
  methods: {
    random_recipe () {
      switch(this.selected) {
        case "Vegetarische Gerichte":
          this.random_list = this.returnList
          break;
        case "Fleischgerichte":
          this.random_list = this.returnList2
          break;
        case "":
          this.$refs.dropDownMenu.classList.add("ani")
          this.random_list = ["wähle eine Kategorie"]
          break
        default:
          if (Object.keys(this.recipeList_veg).includes(this.selected)) {
            this.random_list = this.recipeList_veg[this.selected]
          } else {
            this.random_list = this.recipeList_noveg[this.selected]
          }
      }
      console.log("Random List ", this.random_list)
      var itemIndex = Math.floor((Math.random() * this.random_list.length))
      if (this.random_list[itemIndex] !== this.$refs.recipe_ref.innerHTML) {
        this.$refs.recipe_ref.innerHTML = this.random_list[itemIndex]
      } else if (itemIndex !== 0) {
        this.$refs.recipe_ref.innerHTML = this.random_list[itemIndex - 1]
      } else {
        this.$refs.recipe_ref.innerHTML = this.random_list[itemIndex + 1]
      }
      if (this.random_list[itemIndex] == "wähle eine Kategorie") {
        this.$refs.recipe_ref.innerHTML = this.random_list[itemIndex]
      }
    },
    new_recipe (e) {
      if (e.key === "Enter") {
        this.input_text = this.input_text.replace(/^\s+|\s+$/g, '')
        if (!this.recipeList.includes(this.input_text) && !this.recipeList.includes(this.input_text.slice(0, 30))) {
          if (this.input_text.length > 30) {
            this.recipeList.push(this.input_text.slice(0, 30))
          } else {
            this.recipeList.push(this.input_text)
          }
        }
        this.input_text = "" 
      }
    },
    category () {
      console.log(this.selected)
      switch(this.selected) {
        
        case "Vegetarische Gerichte":
          console.log(this.returnList)
          if (this.returnList.length == 0) {
           Object.values(this.recipeList_veg).forEach(cat => {cat.forEach(meal => {this.returnList.push(meal)})})
          }
          return this.returnList;
        case "Fleischgerichte":
          if (this.returnList2.length == 0) {
            Object.values(this.recipeList_noveg).forEach(cat => {cat.forEach(meal => {this.returnList2.push(meal)})})
          }
          return this.returnList2;
        default:
          if (Object.keys(this.recipeList_veg).includes(this.selected)) {
            return this.recipeList_veg[this.selected]
          } else {
            return this.recipeList_noveg[this.selected]
          }
      }
    },
  }
}

</script>

